.webcam-capture {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 20px;

    &>.btns {
        margin-bottom: 15px;

        &>* {
            padding: 10px 20px;
            margin: 0 10px;
            width: 100px;
            text-align: center;
            font-size: 16px;
            width: 100px;
        }
    }

    & .container {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        flex-wrap: wrap;
        width: 100%;
        margin-bottom: 20px;

        &>.camera {
            margin-bottom: 15px;
            position: relative;

            &>video {
                // height: 200px;
                width: 350px;
            }

            &>.demo {
                position: absolute;
                top: 0;
                left: -70px;
                z-index: 1;
            }
        }

        &>.container-screen-shot {
            position: relative;
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-top: 30px;
            &>.screen-shot {
            //     height: 200px;
                width: 350px;
            }

            &>.demo {
                position: absolute;
                top: 0;
                left: -70px;
                z-index: 1;
            }
        }
    }
}